@font-face {
  font-family: "oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "oswald", "Arial", sans-serif;
  background: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding: 0 20px;
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.button {
  display: inline-block;
  text-decoration: none;
  background-color: #68b738;
  color: #ffffff;
  padding: 10px;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  width: 100%;
  transition: background-color 0.5s;
}
.button:hover {
  background: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background: #5eaa2f;
}

.button--light {
  font-size: 16px;
  background: #f2f2f2;
  color: #000000;
  max-width: 300px;
  transition: background-color 0.5s;
}
.button--light:hover {
  background: #ebebeb;
}
.button--light:active {
  color: rgba(68, 68, 68, 0.3);
}

.main-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header {
    margin-top: 15px;
  }
}
@media (min-width: 1280px) {
  .main-header {
    margin-top: 17px;
  }
}

.main-header__logo {
  padding: 10px 0;
  /*stylelint-disable */
}
.main-header__logo-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.5s;
}
.main-header__logo-text:hover {
  opacity: 0.8;
}
.main-header__logo-text:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-header__logo-text {
    position: static;
    left: 0;
    top: 0;
    transform: translate(0);
  }
}
@media (min-width: 1280px) {
  .main-header__logo-text {
    margin-top: 26px;
  }
}
.main-header__logo-img {
  display: inline-block;
  background-image: url(../img/logo-img.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  width: 34px;
  height: 38px;
  transition: opacity 0.5s;
}
.main-header__logo-img:hover {
  opacity: 0.8;
}
.main-header__logo-img:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-header__logo-img {
    display: none;
  }
}

.nav__toggle {
  display: block;
  cursor: pointer;
  border: none;
  background: none;
  background-image: url(../img/sprite.svg#logo_mobile);
  background-repeat: no-repeat;
  background-position: center;
  width: 24px;
  height: 18px;
  transition-timing-function: ease-in-out;
  transition-duration: 0.5s;
}
.nav__toggle:focus {
  background-image: url(../img/sprite.svg#logo_mobile-close);
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

/* stylelint-enable */
.nav__wrapper {
  position: absolute;
  width: 100%;
  top: 62px;
  left: 0;
  background: #ffffff;
  z-index: 1;
  display: none;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav__wrapper {
    position: static;
    display: flex;
    margin-top: 15px;
    background: none;
  }
}
@media (min-width: 1280px) {
  .nav__wrapper {
    max-width: 600px;
    margin-left: 60px;
    margin-right: 44px;
    margin-top: 35px;
  }
}

.nav__wrapper-active {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  text-align: center;
  align-items: center;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.nav__item {
  list-style: none;
  padding-top: 23px;
  padding-bottom: 19px;
  z-index: 1;
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item {
    padding-bottom: 10px;
    padding-left: 37px;
    border: none;
    padding-top: 0;
  }
}
@media (min-width: 1280px) {
  .nav__item {
    padding-left: 35px;
  }
}

.nav__link {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  transition: opacity 0.5s;
}
.nav__link:hover {
  opacity: 0.6;
}
.nav__link:focus {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1280px) {
  .nav__link {
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: 768px) {
  .nav__link--active {
    border-bottom: 2px solid #68b738;
    padding-bottom: 5px;
  }
}

.nav__link--index:active {
  padding-bottom: 5px;
  border-bottom: 2px solid #ffffff;
}
@media (min-width: 1280px) {
  .nav__link--index {
    color: #ffffff;
  }
}

@media (min-width: 1280px) {
  .nav__link--index.nav__link--active {
    border-bottom: 2px solid #ffffff;
  }
}

.nav {
  background: none;
}

